<template>
    <div class="header">
        <div class="top">
            <router-link to="/"><h2 class="logo">soart</h2></router-link>  
        </div>
    </div>
</template>

<script>
export default {
    name:'header'
}
</script>

<style lang="scss" scoped>
    .header {
        width: 750px;
        position: fixed;
        z-index: 999;
        background-color: #fff;
        top: 0;
        .top {
            width: 750px;
            height: 94px;
            border-bottom: 1px solid #f3f3f3;
            display: flex;
            align-items: center;
            justify-content: center;
        }

    }
</style>



