import Vue from 'vue';
import Vuex from 'vuex';
import * as actions from './action'
import * as getters from './getter'

Vue.use(Vuex);

const state = {
    newData: {hasData:false, data: []}
};

const mutations = {
    INIT_NEW(state, data) {
        state.newData.data = data
        state.newData.hasData = 1
    }
};

export default new Vuex.Store({
    state,
    mutations,
    getters,
    actions
});


