<template>
    <div class="cls">
        <div class="side"></div>
        <search></search>
        <div class="main">
            <div class="menu">
                <ul>
                    <li><a v-popover:foo >综合<span></span></a></li>
                    <li><a>销量</a></li>
                    <li><a>最新</a></li>
                    <li><a>店铺</a></li>
                    <li><a v-popover:stl>{{stlChange}}</a></li>
                </ul>
                   <popover name="foo" :pointer="false" :width="150">
                        <ul class="down">
                            <li><a>1</a></li>
                            <li><a>2</a></li>
                            <li><a>3</a></li>
                            <li><a>4</a></li>
                        </ul>
                    </popover>
                    
                    <popover name="stl" :pointer="false" :width="150">
                        <ul class="down">
                            <li v-on:click="change"><a>大图</a></li>
                            <li v-on:click="change"><a>列表</a></li>
                        </ul>
                    </popover>
                
            </div>
            <ul class="items">
                <li v-for="item in getNew.data" :key="item.id"><router-link to=""><goods :goodsData="item"></goods></router-link></li>
            </ul>
        </div>
    </div>
</template>

<script>
import goods from './goodsDtl'
import search from './search'
import { mapGetters, mapActions } from 'vuex'
export default {
    name: 'gclass',
    components: {
        goods,
        search
    },
    data() {
        return {
            stlChange: ''
        }
    },
    computed: {
        ...mapGetters([
            'getNew'
        ])
    },
    methods: {
        ...mapActions([
            'postNew',
            'getNewData'
        ]),
        change(event) {
            let id
            if (event.path[0].innerHTML == "大图") {
                id = 'big'
            }else if(event.path[0].innerHTML == "列表") {
                id = 'rank'
            }
            this.$router.push({ name: 'empty', params: { id: id }})
        }

    },
    mounted() {
        this.postNew()
        if(this.$route.params.id == "big") {
            this.stlChange = "大图"
        }else {
            this.stlChange = "列表"
        }
    }
}
</script>

<style lang="scss" scoped>
    .cls {
        margin-top: 100px;
        .main {
            .items {
                display: flex;
                align-items: center;
                justify-content: space-around;
                flex-wrap: wrap;
            }
            .menu {
                padding: 0 20px;
                ul {
                    display: flex;
                    align-items: center;
                    justify-content: space-around;
                    li {
                        a {
                            line-height: 60px;
                            font-size: 28px;
                            color: #333;
                            span {
                                font-weight: bold;
                                display: inline-block;
                                border-right: solid 1px #333;
                                border-bottom: solid 1px #333;
                                width: 15px;
                                height: 15px;
                                transform: rotate(45deg);
                                margin: 0 0 5px 10px;
                            }
                        }
                        
                    }
                }
                .down {
                    display: flex;
                    align-items: center;
                    justify-content: space-around;
                    flex-wrap: wrap;
                    width: 150px;
                    li {
                        a {
                            display: block;
                            width: 150px;
                            text-align: center;
                        }
                        a:hover {
                            background-color: #f3f3f3;
                        }
                    }
                }
                
                
            }
        }  
    }
</style>



