<template>
    <swiper :options="swiperOption">
        <swiper-slide v-for="slide in swiperSlides" :key="slide.id"><img :src="slide" alt=""></swiper-slide>
        <!-- <div class="swiper-pagination" slot="pagination"></div> -->
    </swiper>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
    name: 'banner',
    components: {
        swiper,
        swiperSlide
    },
    data() {
      return {
        swiperOption: {
          pagination: {
            el: '.swiper-pagination'
          },
          autoplay: true,
          loop:true
        },
        swiperSlides: [
            './static/banner.jpg',
            './static/banner.jpg',
            './static/banner.jpg',
            './static/banner.jpg',
            './static/banner.jpg'
        ]
      }
    }
}
</script>

<style lang="scss" scoped>
    img {
        width: 750px;
    }
</style>




