import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import gclass from '@/components/gclass'
import empty from '@/components/empty'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/gclass/:id',
      component: gclass
    },
    {
      path: '/empty',
      name: 'empty',
      component: empty
    }
  ]
})



