<template>
  <div id="app">
    <top></top>
    <router-view/>
    <foot></foot>
  </div>
</template>

<script>
import top from './components/mainHeader'
import foot from './components/footer'
import store from './vuex/store'
export default {
  name: 'App',
  components: {
    top,
    foot
  },
  store
}
</script>

<style lang="scss">
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  body {
    font-family: "Helvetica Neue",Helvetica,Roboto,Arial,sans-serif;
  }
  li {list-style: none;}
  a {text-decoration: none;color: #333;}
  #app {
    width: 750px;
    margin: 0 auto;
  }
</style>



