<template>
    <div class="cont">
        <banner></banner>
        <ul class="nav">
            <li v-for="navItem in navData" :key="navItem.id"><router-link to="" class="link"><img :src="navItem.img" alt="">{{navItem.title}}</router-link></li>
        </ul>
        <div class="hot">
            <div class="hotTitle">气人好物</div>
            <ul class="hotItem">
                <li v-for="hotItem in hotData" :key="hotItem.id"><router-link to=""><img class="hotBig" v-if="hotItem.isBig" :src="hotItem.img" alt=""><img v-else :src="hotItem.img" alt=""></router-link></li>
            </ul>
        </div>
        <div class="new">
            <div class="newTitle">最新单品</div>
            <ul class="newItem">
                <li v-for="newItem in getNew.data" :key="newItem.id"><router-link to=""><goods :goodsData="newItem"></goods></router-link></li>
            </ul>
        </div>
        <div>{{db}}</div>
    </div>
</template>

<script>
import banner from './banner'
import goods from './goodsDtl'
import { mapGetters, mapActions } from 'vuex'
export default {
    name: 'indexCont',
    components: {
        banner,
        goods
    },
    computed: {
        ...mapGetters([
            'getNew'
        ])
    },
    methods: {
        ...mapActions([
            'postNew',
            'getNewData'
        ]),
        getItems () {
            // Get data from api
            this.$http.get('http://167.99.105.85:3000/api/tasks').then(
            function (res) {
                this.db = res.body;
            },
            function(err) {
                return console.log(err);
            }
            );
        }
    },
    data() {
        return {
            db: '',
            navData: [
                {
                    title: '绘画',
                    img: './static/logo.png'
                },
                {
                    title: '雕塑',
                    img: './static/logo.png'
                },
                {
                    title: '饰品',
                    img: './static/logo.png'
                },
                {
                    title: '哈哈',
                    img: './static/logo.png'
                }
            ],
            hotData: [
                {
                    title: '绘画',
                    img: './static/banner.jpg',
                    isBig: true
                },
                {
                    title: '绘画',
                    img: './static/banner.jpg',
                    isBig: false
                },
                {
                    title: '绘画',
                    img: './static/banner.jpg',
                    isBig: false
                },
                {
                    title: '绘画',
                    img: './static/banner.jpg',
                    isBig: false
                },

                {
                    title: '绘画',
                    img: './static/banner.jpg',
                    isBig: false
                }

            ]
        }
    },
    mounted() {
        if(this.getNew.hasData != 1) {
            this.postNew()
            
        }
        // this.getItems()
        
        this.$watch("db", function(){
            console.log(this.db)
        }, {deep:true})
    }
}
</script>

<style lang="scss" scoped>
    .cont {
        width: 750px;
        background-color: #eee;
        margin: 200px 0 0 0;
        .nav {
            width: 750px;
            height: 180px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            li {
                display: inline-block;
                width: 180px;
                height: 180px;
                border: 1px solid #999;
                text-align: center;
                background-color: #fff;
                .link {
                    img {
                        width: 120px;
                        height: 120px;
                        margin: 10px 30px;
                    }
                }
            }
        }
        .hot {
            margin-top: 20px;
            background-color: #fff;
            .hotTitle {
                font-size: 28px;
                line-height: 50px;
                padding: 0 20px;
                color: #333;
                border-bottom: 1px solid #f3f3f3;
            }
            .hotItem {
                width: 750px;
                height: 500px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                flex-wrap: wrap;
                li {
                    display: inline-block;
                    height: 246.7px;
                    overflow: hidden;
                    img {
                        width: 246.7px;
                        height: 246.7px;
                    }
                    .hotBig {
                        width: 500px;
                        height: 500px;
                    }
                }
            }
        }
        .new {
            margin-top: 20px;
            background-color: #fff;
            .newTitle {
                font-size: 28px;
                line-height: 50px;
                padding: 0 20px;
                color: #333;
                border-bottom: 1px solid #f3f3f3;
            }
            .newItem {
                display: flex;
                align-items: center;
                justify-content: space-around;
                flex-wrap: wrap;
            }
        }
    }
</style>



