<template>
    <div class="card">
        <div class="image">
            <span></span>
            <img :src="goodsData.image" alt="">
        </div>
        <div class="detail">
            <div class="name">{{goodsData.name}}</div>
            <div class="author"><i>by </i>{{goodsData.author}}</div>
            <span class="price"><span>RMB </span><b>{{goodsData.price}}</b></span>
        </div>
    </div>
</template>

<script>
export default {
    name: 'goodsDtl',
    props: ['goodsData'],
    data() {
        return {
           
        }
    },
    mounted() {
        
    }
}
</script>

<style lang="scss" scoped>
    .card {
        width: 360px;
        border: 1px solid #ccc;
        margin-bottom: 10px;
        .image {
            img {
                width: 360px;
            }
        }
        .detail {
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
            padding: 0 15px;
            .name {
                width: 360px;
                font-weight: bold;
                line-height: 50px;
                font-size: 28px;
                overflow: hidden;
                border-bottom: 1px solid #f3f3f3;
                
            }
            .author {
                line-height: 36px;
                i {
                    color: #999;
                }
            }
            .price {
                span {
                    color: red;
                }
            }
        }
    }
    .card:hover {
        opacity: .7;
    }
</style>



